.App {
  text-align: center;
  display: grid;
  grid-template-columns: repeat(8, 1fr);
  grid-gap: 10px;
  grid-auto-rows: 100px;
  grid-template-areas:
    "a a a a a a a a"
    "c c c c d d d d"
    "c c c c d d d d";
  -ms-flex-align: start;
      align-items: start;
}

.App-logo {
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 75px;
  padding: 20px;
  color: white;
  grid-area: a;
}

.App-title {
  font-size: 1.5em;
}

.leftcolumn {
  grid-area: c;
}

.rightcolumn {
  grid-area: d;
}

.hline { width:100%; height:1px; background: #000 }

p {
    font-family: Arial, Helvetica, sans-serif;
    font-size: 12px;
}



